define('hyperchannel/instance-initializers/remotestorage', ['exports', 'ember'], function (exports, _ember) {
  exports.initialize = initialize;

  function initialize(appInstance) {
    var storage = appInstance.lookup('service:remotestorage');
    var remoteStorage = storage.get('rs');

    // Add to window global for debugging/development
    window.remoteStorage = remoteStorage;

    var rsEvents = ['ready', 'not-connected', 'connected', 'disconnected', 'error', 'features-loaded', 'connecting', 'authing', 'wire-busy', 'wire-done', 'network-offline', 'network-online'];

    rsEvents.forEach(function (evt) {
      remoteStorage.on(evt, function () {
        return _ember['default'].Logger.debug('[remotestorage] RS event: ' + evt);
      });
    });
  }

  exports['default'] = {
    name: 'remotestorage',
    initialize: initialize
  };
});